* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    font-size: 16px;
    font-family: "Numans", "Montserrat", sans-serif;
    --sidebar-colour: #47e5fd;  
    --header-colour: #cacacad3;
}

body {
    display: grid;
    grid-template: 20vh 1fr / 20vw 1fr;
    height: 100vh;
    width: 100vw;
}

.sidebar {
    grid-area: 1 / 1 / 3 / 2;
    background-color: var(--sidebar-colour);
    color: white;
    padding: 10vh 2vw;
    display: flex;
    flex-direction: column;
    align-items: start;
    row-gap: 20px;
}

.sidebar_header {
    display: flex;
    justify-content: space-between;
    align-items: end;
    width: 100%;
    flex-wrap: wrap;
}

.sidebar_header > svg {
    width: 45px;
    fill: white;
    margin-bottom: 4px;
}

h1 {
    font-size: 2.5rem;
}

.lists {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 15px;
}

.list {
    font-size: 1.5rem;
    cursor: pointer;
    width: fit-content;
}

.list_container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#new_list_button {
    padding: 10px 15px;
}

.list_edit_icon {
    cursor: pointer;
    width: 25px;
}



.header {
    grid-area: 1 / 2 / 2 / 3;
    background-color: var(--header-colour);
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 5vw;
}

h2 {
    font-size: 2.5rem;
}

button {
    font-family: inherit;
    color: white;
    background: none;
    border: 1px solid white;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
}

h3 {
    cursor: pointer;
}

#new_task_button {
    width: 60px;
    fill: white;
    cursor: pointer;
}

.content {
    padding: 30px 5vw;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
}

.task {
    display: grid;
    grid-template-columns: 3fr 1fr;
    box-shadow: 0px 1px 3px 0px var(--header-colour) ;
    padding: 10px;
    border-radius: 5px;
}

.task:hover {
    background-color: rgb(231, 231, 231);
}

.task_info_container {
    display: grid;
    grid-template-columns: 2fr 1fr repeat(2, auto) 2fr;
    justify-items: center;
    align-items: center;
    column-gap: 10px;
}

.task_description_container {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.task_title {
    font-size: 1.5rem;
    font-weight: 500;
}

.task_description {
    font-style: italic;
}

.task_form_container, .list_form_container {
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: -2px 3px 30px -9px black ;
    background-color: white;
    z-index: 1;
    flex-direction: column;
    justify-content: center;
    align-items: end;
    width: 25vw;
    padding-bottom: 25px;
    border-radius: 10px;
}

.task_icon {
    width: 18px;
    margin-bottom: 2px;
    cursor: pointer;
}

.task_check_box {
    height: 20px;
    align-self: center;
}

.form_header {
    width: 100%;
    background-color: var(--header-colour);
    padding: 25px;
    display: flex;
    justify-content: space-between;
    font-weight: 600;
    font-size: 1.2rem;
    border-radius: 10px 10px 0px 0px;
}

#form_close_button, #list_close_button {
    cursor: pointer;
}

form {
    display: flex;
    flex-direction: column;
    align-items: start;
    row-gap: 15px;
    width: 100%;
    padding: 20px 25px;
}

input {
    width: 100%;
    border: 1px black solid;
    height: 45px;
    border-radius: 8px;
    font-size: 1rem;
    padding: 10px;
    font-weight: 100;
}

#task_submit_button, #list_submit_button{
    color: white;
    background-color: var(--sidebar-colour);
    margin: 10px 25px 0px 0px;
    padding: 13px 25px;
    font-weight: 600;
}

.list_remove_button {
    background-color: rgb(190, 21, 21);
    margin-top: 10px;
    padding: 13px 28px;
    font-weight: 600;
    color: white;
}

.list_remove_button:hover {
    background-color: rgb(158, 19, 19);
}

#list_submit_button:hover, #task_submit_button:hover {
    background-color: #1eb9d1;
}

.button_container {
    display: flex;
    align-items: center;
    justify-content: end;
    column-gap: 10px;
}

.form_overlay {
    position: absolute;
    width: 100vw;
    height: 100vh;
    background-color: rgba(128, 128, 128, 0.582);
    display: none;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":";AAIA;IACI,UAAU;IACV,SAAS;IACT,sBAAsB;AAC1B;;AAEA;IACI,eAAe;IACf,+CAA+C;IAC/C,yBAAyB;IACzB,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,kCAAkC;IAClC,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,wBAAwB;IACxB,uCAAuC;IACvC,YAAY;IACZ,iBAAiB;IACjB,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,gBAAgB;IAChB,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,WAAW;AACf;;;;AAIA;IACI,wBAAwB;IACxB,sCAAsC;IACtC,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,oBAAoB;IACpB,YAAY;IACZ,gBAAgB;IAChB,uBAAuB;IACvB,aAAa;IACb,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,iBAAiB;IACjB,aAAa;IACb,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,iDAAiD;IACjD,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,aAAa;IACb,kDAAkD;IAClD,qBAAqB;IACrB,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,qCAAqC;IACrC,uBAAuB;IACvB,UAAU;IACV,sBAAsB;IACtB,uBAAuB;IACvB,gBAAgB;IAChB,WAAW;IACX,oBAAoB;IACpB,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,sCAAsC;IACtC,aAAa;IACb,aAAa;IACb,8BAA8B;IAC9B,gBAAgB;IAChB,iBAAiB;IACjB,gCAAgC;AACpC;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,aAAa;IACb,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,uBAAuB;IACvB,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,uCAAuC;IACvC,yBAAyB;IACzB,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,kCAAkC;IAClC,gBAAgB;IAChB,kBAAkB;IAClB,gBAAgB;IAChB,Y